import React, { Component } from 'react';
import './Qcard.css';

class QCard extends Component {


    options = [];
    checkboxes = [];
    userCorrect = [];
    renderOptions() {
        var incorrects = this.props.incorrect;
        for (let i = 0; i < incorrects.length; i++)
            this.options.push(incorrects[i]);
        this.options.push(this.props.correct);
    }
    shuffle(arra1) {
        var ctr = arra1.length, temp, index;
        while (ctr > 0) {
            index = Math.floor(Math.random() * ctr);
            ctr--;
            temp = arra1[ctr];
            arra1[ctr] = arra1[index];
            arra1[index] = temp;
        }
        return arra1;
    }
    getAllCheckboxes() {
        var inputs = document.getElementsByTagName("input");
        for (let i = 0; i < inputs.length; i++) {
            if (inputs[i].type === "checkbox")
                this.checkboxes.push(inputs[i]);
        }
    }
    uncheckPreviousId(id) {
        document.getElementById(id).checked = false;
    }
    removePreviousIds(option, id) {
        for (let i = 0; i < this.props.selectedIds.length; i++) {
            if (this.props.selectedIds[i].includes(id.split("-o-")[0])) {
                this.uncheckPreviousId(this.props.selectedIds[i]);
                this.props.selectedIds[i] = id;
                this.props.selectedAns[i] = option;
                return;
            }
        }
        this.props.selectedIds.push(id);
        this.props.selectedAns.push(option);
    }
    checkDoubleOptions(option, id) {
        this.getAllCheckboxes();
        if (!document.getElementById(id).checked) {
            let index = this.props.selectedIds.indexOf(id);
            if (index > -1) {
                this.props.selectedIds.splice(index, 1);
                this.props.selectedAns.splice(index, 1);
                return;
            }
        }
        this.removePreviousIds(option, id);
    }
    calculateScore() {
        console.log(this.props.selectedAns);
        console.log(this.props.corrects);
        if (this.props.selectedAns.length !== 0) {
            for (let i = 0; i < this.props.selectedAns.length; i++) {
                if (this.props.corrects.includes(this.props.selectedAns[i])) {
                    this.userCorrect.push(i);
                }
            }
        }
        console.log(this.userCorrect.length);
    }
    displayOptions(id) {
        const shuffledArray = this.shuffle(this.options);
        return (
            shuffledArray.map((key, index) =>
                <button key={index} className="question-option" >
                    <div className="option-check-cont">
                        <div className="check-cont">
                            <input type="checkbox" id={"q-" + id + "-o-" + index} className="regular-checkbox" onChange={() => this.checkDoubleOptions(key, "q-" + id + "-o-" + index)} />
                        </div>
                        <div className="option-text">&nbsp;&nbsp;{key}</div>
                    </div>
                </button>
            ));
    }
    showNextQuestion(id) {
        var nextid = id + 1;
        console.log("next id is ", nextid);
        if (id === 9) {
            return;
        }
        document.getElementById("qcard" + id).classList.add('hide');
        document.getElementById("qcard" + nextid).classList.remove("hide");
    }
    displayScoreCard(id) {
        this.calculateScore();
        document.getElementById("qcard" + id).classList.add('hide');
        document.getElementById("scorecard").classList.remove("hide");
        const x = this.userCorrect.length;
        document.getElementById("score").innerHTML = x;
        let src = "";
        if (x === 10) {
            src = "https://media.giphy.com/media/3ohhwlEsgzUPnBqOR2/giphy.gif";
        } else if (x === 9) {
            src = "https://media.giphy.com/media/3ohhwx480vzXoISaTS/giphy.gif";
        } else if (x === 8 || x === 7) {
            src = "https://media.giphy.com/media/26n79OaNx8sClwFby/giphy.gif";
        } else if (x === 6 || x === 5) {
            src = "https://media.giphy.com/media/l1J9wQIaj0He3bTSU/giphy.gif";
        } else if (x === 4 || x === 3) {
            src = "https://media.giphy.com/media/26n79rR00OkRLJCLu/giphy.gif";
        } else if (x === 2 || x === 1) {
            src = "https://media.giphy.com/media/3ohhwiuD4hmKIVdfzy/giphy.gif";
        } else {
            src = "https://media.giphy.com/media/l1J9NJRqBAp3iHaxO/giphy.gif";
        }
        document.getElementById("result-img").src = src;
    }
    renderAllQuestions() {
        return (
            <React.Fragment>
                <div className={"shadow " + (this.props.no === 0 ? "" : "hide")} id={this.props.id}>
                    <div className="question-cont">
                        <p>{this.props.no + 1}.  &nbsp;  {this.props.question}</p>
                    </div>
                    <div className="img-remain-cont">

                        <div className="remain-cont">
                            <div className="options-con">
                                {this.displayOptions(this.props.id + 1)}
                            </div>
                            <div className="btn-cont">
                                <button className={"circle-text button-design " + (this.props.no === 9 ? "disabled" : "yellow-back")} id={"nextbtn" + this.props.no} onClick={() => this.showNextQuestion(this.props.no)}>Next Question</button>
                                <button className="red-back circle-text button-design" onClick={() => this.displayScoreCard(this.props.no)}> Finish Quiz</button>
                            </div>
                        </div>
                        <div className="gif-cont">
                            <img src="https://media.giphy.com/media/3ohhwEVKBOZd7bCQ7u/giphy.gif" alt="" />
                        </div>
                    </div>
                </div>

            </React.Fragment>
        );
    }
    renderFirstQuestion() {
        console.log("inside render first question");
        var qcards = document.getElementsByClassName("shadow");
        for (let i = 0; i < qcards.length; i++) {
            if (i === 0) {
                console.log(document.getElementById("qcard-" + i));
                document.getElementById("qcard-" + i).className += 'hide';
            }
        }
    }
    render() {
        this.renderOptions();
        return (
            this.renderAllQuestions()
        );

    }
}

export default QCard;