import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Loader from '../Loader/Loader';
import QCard from '../QCard/QCard';
import './StudentPortal.css';

class StudentPortal extends Component {

    constructor(props) {
        super(props);
        this.state = {
            questions: [],
            isloading: true,
            error: null
        }
    }
    selectedIds = [];
    selectedAns = [];
    correctAns = [];
    componentDidMount() {
        fetch("https://opentdb.com/api.php?amount=10&category=18&type=multiple")
            .then(res => res.json())
            .then(
                (result) => {
                    const newquestions = result.results;
                    const newloading = false;
                    this.setState(state => {
                        if (state.questions === newquestions) {
                            return null;
                        } else {
                            state.questions = newquestions;
                            state.isloading = newloading;
                            return { newquestions };
                        }
                    });
                },
                (error) => {
                    this.setState({
                        isLoading: true,
                        error
                    });
                }
            )
    }

    renderQuestions() {
        console.log("Called...", this.state.isloading);
        if (this.state.isloading)
            return <Loader />
        else {
            return (
                this.state.questions.map((key, index) => {
                    this.correctAns.push(key.correct_answer);
                    return <QCard key={index} id={"qcard" + index} no={index} question={key.question} correct={key.correct_answer} incorrect={key.incorrect_answers} selectedIds={this.selectedIds} selectedAns={this.selectedAns}
                        corrects={this.correctAns} />
                }));
        }
    }
    uncheckAll() {
        var inputs = document.getElementsByTagName("input");
        for (let i = 0; i < inputs.length; i++) {
            if (inputs[i].type === "checkbox")
                inputs[i].checked = false;
        }
    }
    againTest() {
        this.uncheckAll();
        this.selectedAns = [];
        this.selectedIds = [];
        document.getElementById("scorecard").classList.add('hide');
        document.getElementById("qcard0").classList.remove("hide");
    }
    renderScoreCard() {
        return (
            <div id="scorecard" className="hide scorecard">
                <div className="scorecard-heading">
                    <h1 className="circle-text">Score Card</h1>
                </div>
                <div className="score-img">
                    <img id="result-img" alt="" />
                </div>
                <div className="display-score">
                    <h3 className="score-text-design score-label">Your Score : </h3>
                    <h1 id="score" className="score-text-design"></h1>
                    <div className="line"></div>
                    <h1 className="score-text-design">10</h1>
                </div>
                <div className="links scorecard-heading scorecard-btn-cont">
                    <Link to="/test"><button className="btn mr-2 yellow-back"><i class="fas fa-redo"></i>&nbsp;Test Yourself Again</button></Link>
                    <Link to="/"><button className="btn red-back"><i class="fas fa-home"></i>&nbsp;Go Back to Home</button></Link>
                </div>
            </div>
        );
    }

    render() {
        return (
            <React.Fragment>
                <div className="test-cont">
                    {this.renderQuestions()}
                </div>
                {this.renderScoreCard()}
            </React.Fragment>
        );
    }
}

export default StudentPortal;