import React, { Component } from "react";
import Header from "./components/Header/Header";
import { Switch, Route } from 'react-router-dom';
import Firebase from "firebase";
import "./App.css";
import StudentPortal from "./components/MainContent/StudentPortal/StudentPortal";
import ChooseScreen from "./components/MainContent/ChooseScreen/ChooseScreen";
import TeacherPortal from "./components/TeacherPortal/TeacherPortal";
import config from "./config";
import ContactPage from "./components/Contact/Contact";

class App extends Component {
  constructor(props) {
    super(props);
    Firebase.initializeApp(config);
  }
  render() {
    return (
      <div>
        <Header />
        <ChooseScreen />
        <Switch>
          <Route path='/test' component={StudentPortal} />
          <Route path="/createquiz" component={TeacherPortal} />
          <Route path="/contact" component={ContactPage} />
          <Route exact path='/' component={ChooseScreen} />
        </Switch>
      </div >
    );
  }

}

export default App;
