const questionOperations = {
    questions: [],
    add(question) {
        console.log("Inside add Question function..", this.questions);
        this.questions.push(question);
    },
    mark(id) {
        var question = this.search(id);
        question.markForDelete = !question.markForDelete;
    },
    search(id) {
        console.log(this.questions);
        for (let i = 0; i < this.questions.length; i++) {
            if (this.questions[i] !== undefined) {
                if (this.questions[i].id === id)
                    return this.questions[i];
            }
        }
        return [];
    },
    update(questionid, questionObject) {
        for (let i = 0; i < this.questions.length; i++) {
            if (this.questions[i] !== undefined) {
                if (this.questions[i].id === questionid)
                    this.questions[i] = questionObject;
            }
        }
    },
    sort(key, order) {
        console.log("Inside Sort Question Function..");
        if (key === 'id' || key === 'score') {
            if (order === 'a' || order === 'A') {
                return this.questions.sort((first, second) => first[key] - second[key]);
            } else {
                return this.questions.sort((first, second) => second[key] - first[key]);
            }
        } else {
            if (order === 'a' || order === 'A') {
                return this.questions.sort((first, second) => first[key].localeCompare(second[key]));
            } else {
                return this.questions.sort((first, second) => second[key].localeCompare(first[key]));
            }
        }
    },
    delete() {
        console.log("Inside Delete", this.questions);
        return this.questions = this.questions.filter(question => question.markForDelete);
    },
    unMarkCount() {
        return this.totalCount() - this.markCount();
    },
    markCount() {
        return this.questions.filter(questionObject => questionObject.markForDelete).length;
        // return 0;
    },
    totalCount() {
        let count = 0;
        for (let i = 0; i < this.questions.length; i++) {
            if (this.questions[i] !== undefined)
                count++;
        }
        return count;
    },
    nextQuestionid() {
        if (this.questions.length === 0)
            return 1;
        return parseInt(this.questions[this.questions.length - 1].id) + 1;
    }
}
export default questionOperations;