import React, { Component } from 'react';
import firebase from "firebase";
import questionOperations from '../../questionOperations';
import Question from '../../question';
import './TeacherPortal.css';
import EmptyFieldModal from '../EmptyFieldsModal/EmptyFieldModal';
import NoDataModal from '../NoDataModal/NoDataModal';
import DataUploadedModal from '../../DataUploadedModal/DataUploadedModal';

class TeacherPortal extends Component {

    questionObject = {};
    componentDidMount() {
        this.loadFromServer();
        this.displayCount();
        this.hidediv();
    }
    loadCount(count) {
        document.getElementById("id").innerText = count;
    }
    hidediv() {
        var div = document.querySelector("#sortDiv");
        div.className = "hide";
    }
    deleteRecords = () => {
        var deletedQuestions = questionOperations.delete();
        for (let i = 0; i < deletedQuestions.length; i++) {
            firebase.database().ref("questions").child(deletedQuestions[i].id).remove();
            this.displayCount();
            this.loadFromServer();
        }
    }
    printRecords(questions) {
        document.querySelector("#questions").innerHTML = "";
        questions.forEach(question => this.print(question));
        this.displayCount();
    }
    showHide() {
        var div = document.querySelector("#sortDiv");
        div.classList.toggle("hide");
    }
    doSort() {
        var sortBy = document.querySelector("#sortBy").value;
        var order = document.querySelector("#order").value;
        if (sortBy === "-1")
            return;
        if (order === "asc") {
            this.printRecords(questionOperations.sort(sortBy, 'A'));
        } else {
            this.printRecords(questionOperations.sort(sortBy, 'B'));
        }
    }
    snapshotToArray(snapshot) {
        let returnArr = [];
        snapshot.forEach(function (childSnapshot) {
            var item = childSnapshot.val();
            item.key = childSnapshot.key;

            returnArr.push(item);
        });
        return returnArr;
    };
    loadFromServer() {
        let objects = [];
        var questions = firebase.database().ref("questions");
        questions.on('value', (snapshot) => {
            objects = this.snapshotToArray(snapshot);
            if (objects === null) {
                questionOperations.questions = [];
                this.loadCount("1");
                return;
            }
            questionOperations.questions = objects;
            this.printRecords(questionOperations.questions);
            this.loadCount(questionOperations.nextQuestionid());
        });
    }
    savetoserver(questionObject) {
        var pr = firebase.database().ref('questions/' + questionObject.id).set(questionObject);
        pr.then(res => {
        }).catch(err => {
            alert("Record not Saved :(");
            console.log("Error is ", err);
        })

    }
    save() {
        if (localStorage) {
            var json = JSON.stringify(questionOperations.questions);
            localStorage.questions = json;
            window.$('#dataLoadedModal').modal('show');
        } else {
            alert("Your Browser Is OutDated :(");
        }
    }
    load() {
        if (questionOperations.questions.length !== 0) {
            this.printRecords(questionOperations.questions);
        } else {
            window.$('#NoDataModal').modal('show');
            this.loadCount("1");
        }
    }
    toggleMark() {
        var questionid = this.getAttribute("qid");
        var tr = this.parentNode.parentNode;
        tr.classList.toggle("alert-danger");
        questionOperations.mark(questionid);
        document.querySelector("#total").innerText = questionOperations.totalCount();
        document.querySelector("#mark").innerText = questionOperations.markCount();
        document.querySelector("#unmark").innerText = questionOperations.unMarkCount();
    }
    clearAll() {
        var inputs = document.getElementsByTagName("input");
        for (let i = 0; i < inputs.length; i++) {
            inputs[i].value = "";
        }
        document.getElementById("name").value = "";
        this.loadCount(questionOperations.nextQuestionid());
    }
    edit() {
        var id = this.getAttribute("qid");
        this.questionObject = questionOperations.search(id);
        for (let key in this.questionObject) {
            if (key === 'id') {
                document.querySelector("#" + key).innerText = this.questionObject[key];
                continue;
            }
            if (key === 'markForDelete' || key === "key") {
                continue;
            }
            document.querySelector("#" + key).value = this.questionObject[key];
        }
    }
    updateChanges() {
        var questionObject = new Question();
        let id;
        for (let key in questionObject) {
            if (key === 'markForDelete' || key === "key") {
                continue;
            }
            if (key === 'id') {
                questionObject[key] = document.querySelector("#" + key).innerText;
                id = questionObject[key];
                continue;
            }
            questionObject[key] = document.querySelector("#" + key).value;
        }
        questionOperations.update(id, questionObject);
        this.load();
        this.saveEditedChange(id);
        this.clearAll();

    }
    saveEditedChange(questionid) {
        firebase.database().ref(`questions/${questionid}`).set(questionOperations.search(questionid));
    }
    createIcon(className, fn, id) {
        var i = document.createElement("i");
        i.className = className;
        i.addEventListener("click", fn);
        i.setAttribute("qid", id);
        return i;
    }
    displayCount() {
        document.querySelector("#total").innerText = questionOperations.totalCount();
        document.querySelector("#mark").innerText = questionOperations.markCount();
        document.querySelector("#unmark").innerText = questionOperations.unMarkCount();
    }
    addQuestion() {
        var questionObject = new Question();
        for (let key in questionObject) {
            if (key === 'markForDelete') {
                continue;
            }
            if (key === 'id') {
                questionObject[key] = document.querySelector("#" + key).innerText;
                continue;
            }
            questionObject[key] = document.querySelector("#" + key).value;
            if (questionObject[key] === "") {
                window.$('#emptyFieldModal').modal('show');
                return;
            }
        }
        questionOperations.add(questionObject);
        this.savetoserver(questionObject);
        this.clearAll();
        this.load();
        this.loadCount(questionOperations.nextQuestionid());

    }
    print(question) {
        if (question.markForDelete) {
            return;
        }
        var index = 0;
        var tbody = document.querySelector("#questions");
        var tr = tbody.insertRow();
        for (let key in question) {
            if (key === "markForDelete" || key === "key") {
                continue;
            }
            tr.insertCell(index).innerText = question[key];
            index++;
        }
        var td = tr.insertCell(index);
        td.className = "text-center";
        td.appendChild(this.createIcon("fas fa-trash-alt mr-4", this.toggleMark, question.id));
        td.appendChild(this.createIcon("fas fa-edit mr-4", this.edit, question.id));
    }
    unHook() {
        document.querySelector("#add").removeEventListener("click");
    }
    render() {
        return (
            <React.Fragment>
                <div className="container">
                    <h1 className="design-heading text-center">Question CRUD</h1>
                    <div className="form-group">
                        <label htmlFor="id">Question Id :</label>
                        <label className="form-control" id="id"></label>
                    </div>
                    <div className="form-group">
                        <label htmlFor="name">Name :</label>
                        <textarea className="form-control" placeholder="Type Question Here" id="name" rows="6" cols="20" ></textarea>
                    </div>
                    <div className="form-group">
                        <label htmlFor="optionA">Option A :</label>
                        <input type="text" className="form-control" placeholder="Type Option A" id="optionA" />
                    </div>
                    <div className="form-group">
                        <label htmlFor="optionB">Option B :</label>
                        <input type="text" className="form-control" placeholder="Type Option B" id="optionB" />
                    </div>
                    <div className="form-group">
                        <label htmlFor="optionC">Option C :</label>
                        <input type="text" className="form-control" placeholder="Type Option C" id="optionC" />
                    </div>
                    <div className="form-group">
                        <label htmlFor="optionD">Option D :</label>
                        <input type="text" className="form-control" placeholder="Type Option D" id="optionD" />
                    </div>
                    <div className="form-group">
                        <label htmlFor="rightAns">Right Answer :</label>
                        <input type="text" className="form-control" placeholder="Type Right Answer" id="rightAns" />
                    </div>
                    <div className="form-group">
                        <label htmlFor="score">Score :</label>
                        <input type="range" min="1" max="5" className="custom-range" id="score" />
                    </div>
                    <div className="btn-groupt text-center">
                        <button className="btn btn1 mr-2 " id="add" onClick={() => this.addQuestion()}><i className="fa fa-plus-square" aria-hidden="true"></i>&nbsp; Add</button>
                        <button className="btn btn1 mr-2 " id="delete" onClick={() => this.deleteRecords()}><i className="fa fa-trash" aria-hidden="true"></i>&nbsp; Delete</button>
                        <button className="btn btn1 mr-2" id="sort" onClick={() => this.showHide()}><i className="fas fa-sort-amount-down"></i>&nbsp;Sort</button>
                        <button className="btn btn1 mr-2" id="load" onClick={() => this.load()}><i className="fa fa-spinner" aria-hidden="true"></i>&nbsp;Load</button>
                        <button className="btn btn1 mr-2" id="update" onClick={() => this.updateChanges()}><i className="far fa-edit"></i>&nbsp;Update</button>
                        <button className="btn btn1 mr-2" id="savetoserver" onClick={() => this.save()}><i className="fas fa-cloud-upload-alt"></i>&nbsp;Save to Server</button>
                        <button className="btn btn1 mr-2" id="clearall" onClick={() => this.clearAll()} > <i className="fas fa-square"></i>&nbsp;Clear All</button>
                    </div>
                    <div id="sortDiv">
                        <div id="flex-cont">
                            <div className="input-group mb-3" id="cont1">
                                <div className="input-group-prepend">
                                    <label className="input-group-text" htmlFor="sortBy">Sort By</label>
                                </div>
                                <select className="custom-select" id="sortBy" onChange={() => this.doSort()} >
                                    <option value="-1">Choose...</option>
                                    <option value="id">Id</option>
                                    <option value="name">Name</option>
                                    <option value="score">Score</option>
                                </select>
                            </div>
                            <div className="input-group mb-3" id="cont2">
                                <div className="input-group-prepend">
                                    <label className="input-group-text" htmlFor="oder">Order</label>
                                </div>
                                <select className="custom-select" id="order" onChange={() => this.doSort()}>
                                    <option value="asc" selected>Ascending</option>
                                    <option value="desc">Descending</option>
                                </select>
                            </div>
                        </div>
                    </div>
                    <div id="counter">
                        <button className="btn btn-info m-4 flex1" >Total  Records :<span id="total"></span></button>
                        <button className="btn btn-info m-4 flex1">Marked  Records :<span id="mark"></span></button>
                        <button className="btn btn-info m-4 flex1">Unmarked  Records :<span id="unmark"></span></button>

                    </div>
                    <table className="table table-bordered">
                        <thead className="thead-dark">
                            <tr>
                                <th className="text-center">Id</th>
                                <th className="text-center">Name</th>
                                <th className="text-center">Option A</th>
                                <th className="text-center">Option B</th>
                                <th className="text-center">Option C</th>
                                <th className="text-center">Option D</th>
                                <th className="text-center">Right Ans</th>
                                <th className="text-center">Score</th>
                                <th className="text-center">Operations</th>
                            </tr>
                        </thead>
                        <tbody id="questions">
                        </tbody>
                    </table>
                </div>
                <EmptyFieldModal />
                <NoDataModal />
                <DataUploadedModal />
            </React.Fragment>
        );
    }
}

export default TeacherPortal;