import React, { Component } from "react";
import student from "../../../images/c2.png";
import teacher from "../../../images/t3.jpg";
import { Link } from 'react-router-dom';
import "./ChooseScreen.css";

class ChooseScreen extends Component {
  render() {
    return (
      <div>
        <div id="heading">
          <h1>Choose One . . .</h1>
        </div>
        <div className="options">
          <div className="option">
            <div className="option-card">
              <div id="f1_container">
                <div id="f1_card" >
                  <div className="front face" >
                    <img src={student} className="option-img" alt="" />
                    <h1 className="circle-text"> I am a Student !!</h1>

                  </div>
                  <div className="back face center">
                    <h2 className="circle-text">Test your Skills Here !!</h2>
                    <Link to='/test' ><button className="btn-design">Take Test</button></Link>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="option">
            <div className="option-card">
              <div id="f1_container">
                <div id="f1_card" >
                  <div className="front face" >
                    <img src={teacher} className="option-img" alt="" />
                    <h1 className="circle-text"> I am a Teacher !!</h1>
                  </div>
                  <div className="back face center">
                    <h2 className="circle-text">Create your Quiz Here !!</h2>
                    <Link to='/createquiz'><button className="btn-design">Create Quiz</button></Link>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default ChooseScreen;
